@use "global" as *;

.blog {
  position: relative;
  padding-top: rem(84);

  @include mq(md) {
    padding-top: rem(100);
  }
}

.blog::after {
  @include mq(md) {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60.5%;
    background-image: url(../images/common/blog-bg_1.webp);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.blog__inner.inner {
  padding-right: rem(21);
  padding-left: rem(21);

  @include mq(md) {
    padding-right: $padding-pc;
    padding-left: $padding-pc;
  }
}

.blog__title {
  @include mq(md) {
    position: relative;
    z-index: 20;
    width: fit-content;
    margin-inline: auto;
    background-image: radial-gradient(rgba($color: $blue, $alpha: 50%), rgba($color: $blue, $alpha: 20%));
    border-radius: rem(12);
  }
}

.blog__items {
  margin-top: rem(28);

  @include mq(md) {
    margin-top: rem(80);
  }
}

.blog__btn {
  text-align: center;
  margin-top: rem(40);

  @include mq(md) {
    margin-top: rem(80);
  }
}
