button,
[type=button],
[type=submit] {
  /*↓リセットCSS */
  margin: 0;
  background-image: none;
  width: auto;
  display: inline-block;
  text-decoration: none;
  border: none;
  border-radius: 0;
  /*↓カスタム */
  padding: 1.25rem 4.5rem;
