[type=radio] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.75rem;
  margin: 0 2.1875rem 0 0;
  font-size: 0.75rem;
  line-height: 1;
  cursor: pointer;
}
[type=radio] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #707070;
  border-radius: 50%;
  width: 1.125rem;
  height: 1.125rem;
  display: block;
}
[type=radio] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=radio]:checked + span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0.1875rem;
  transform: translateY(-50%);
  background: #391E88;
  border-radius: 50%;
  width: 0.75rem;
  height: 0.75rem;
  display: block;
  opacity: 1;
}

[type=checkbox] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.375rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1;
  cursor: pointer;
  transition: 0.3s ease;
}
[type=checkbox] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #3f72af;
  width: 0.875rem;
  height: 0.875rem;
  display: block;
}
[type=checkbox] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  opacity: 1;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: translateY(-45%) rotate(-35deg);
  width: 0.6125rem;
  height: 0.30625rem;
  margin-top: -0.1em;
  margin-left: 0.1em;
  border-left: 0.125rem solid #C94800;
  border-bottom: 0.125rem solid #C94800;
}

[type=checkbox]:focus + span::before {
  outline: 1px solid #391E88;
}

