@use "global" as *;

// 最終_FB：追加
.plans-category {
  display: flex;
  flex-wrap: wrap;
  gap: rem(16) rem(21);

  @include mq(md) {
    gap: rem(16) rem(32); // 最終_FB(2)：縦方向の余白が大きい ⇒ 2rem → 1rem
    width: 100%;
  }
}

.plans-category__link {
  width: calc((100% - rem(21)) / 2);
  font-size: rem(14);
  font-weight: $normal;
  line-height: 1.571;
  color: $blueaccent;
  text-align: center;
  text-transform: uppercase;
  padding: rem(6) rem(4) rem(4);
  border: rem(1) solid currentColor;
  background-color: $white;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;

  @include mq(md) {
    width: rem(162);
  }
}

.plans-category__link.is-active {
  color: $white;
  border-color: transparent;
  background-color: $blueaccent;
}

.plans-category__link:hover {
  color: $white;
  border-color: transparent;
  background-color: $blueaccent;
  opacity: 1;
}
